import React from 'react';

import './Input.css';

const input = props => (
  <div className="input">
    {props.label && <label htmlFor={props.id}>{props.label}</label>}
    {props.control === 'input' && (
      <input
        className={[
          !props.valid ? 'invalid' : 'valid',
          props.touched ? 'touched' : 'untouched'
        ].join(' ')}
        type={props.type}
        id={props.id}
        required={props.required}
        value={props.value}
        placeholder={props.placeholder}
        onChange={e => props.onChange(props.id, e.target.value, e.target.files)}
        onBlur={props.onBlur}
      />
    )}
    {props.control === 'textarea' && (
      <textarea
        className={[
          !props.valid ? 'invalid' : 'valid',
          props.touched ? 'touched' : 'untouched'
        ].join(' ')}
        id={props.id}
        rows={props.rows}
        required={props.required}
        value={props.value}
        onChange={e => props.onChange(props.id, e.target.value)}
        onBlur={props.onBlur}
      />
    )}
    {props.control === 'list' && (
      <>
        <input
          className={[
            !props.valid ? 'invalid' : 'valid',
            props.touched ? 'touched' : 'untouched'
          ].join(' ')}
          list={props.list}
          type={props.type}
          id={props.id}
          required={props.required}
          value={props.value}
          placeholder={props.placeholder}
          onChange={e => props.onChange(props.id, e.target.value, e.target.files)}
          onBlur={props.onBlur}
        />
        <datalist id="status">
          {props.options.map(status=>{
            return(
              <option value={status}/>
            )
          })}
        </datalist>
        {/* <select 
          style={{display:"block",width:"inherit",textAlign:"center",height:"35px"}}
          name="status"
          className={[
            !props.valid ? 'invalid' : 'valid',
            props.touched ? 'touched' : 'untouched'
          ].join(' ')}
          onChange={e => props.onChange(props.id, e.target.value, e.target.files)}
          onBlur={props.onBlur}>
          {props.options.map((status,i)=>{
            if(status === props.value){
              return(
                <option value={status} key={i} selected>{status}</option>
              )
            } else {
              return(
                <option value={status} key={i}>{status}</option>
              )
            }
          })}
        </select> */}
      </>
    )}
  </div>
);

export default input;
